
// npm i react-chartjs-2

import React from 'react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Doughnut } from 'react-chartjs-2';

ChartJS.register(ArcElement, Tooltip, Legend);

export function ChartBuyer({numStatusPending, numStatusApproved, numStatusDeclined}) {

    const data = {
        labels: ['Red','Orange', 'Green'],
        datasets: [
            {
                label: 'Number of stays',
                data: [numStatusDeclined,numStatusPending,numStatusApproved],
                backgroundColor: [
                    'rgba(255, 99, 132, 0.2)',
                    'rgba(255, 159, 64, 0.2)',
                    'rgba(75, 192, 192, 0.2)',
                   
                ],
                borderColor: [
                    'rgba(255, 99, 132, 1)',
                    'rgba(255, 159, 64, 1)',
                    'rgba(75, 192, 192, 1)',
                    
                ],
                borderWidth: 1,
            },
        ],
    };
    return (
        <div className='Total-info-buyer'>
            <Doughnut data={data} />
        </div>
    )
}
